import { Message } from 'element-ui'
export const baseURL = process.env.NODE_ENV !== 'production' ? '/api/' : ''
// export const baseURL = 'http://192.168.11.237:8086/subcenters/'

/**
 * API
 *
 * @export
 * @param {*} code
 * @param {*} type
 * @param {*} cb
 */
export function apiReturnType(code, type, cb) {
  if (code > 0) {
    Message.success(`${type}`)
    if (typeof cb === 'function') cb()
  } else if (type === '') {
    if (code === -3) Message.warning(`${type}`)
    else Message.error(`${type}`)
  } else if (code === 0) Message.error(`${type}`)
  else if (code === -1) Message.warning(`${type}`)
}
