/**
 * 
 *
 * @export
 * @param {*} value 
 * @returns
 */
export function formatterVehicleType(value) {
  if (value == 0) return ''
  else if (value == 1) return ''
  return ''
}

/**
 * element.table
 *
 * @export
 * @param {*} row
 * @param {*} col
 * @param {*} value 
 * @returns
 */
export function formatterTVehicleType(row, col, value) {
  return formatterVehicleType(value)
}
/**
 * 
 *
 * @export
 * @param {*} value 
 * @returns
 */
export function formatterOnlineType(value) {
  if (value == 0) return ''
  else if (value == 1) return ''
  return ''
}

/**
 * element.table
 *
 * @export
 * @param {*} row
 * @param {*} col
 * @param {*} value 
 * @returns
 */
export function formatterTOnlineType(row, col, value) {
  return formatterOnlineType(value)
}

/**
 * 
 *
 * @export
 * @param {*} value 
 * @returns
 */
export function formatterDirectionType(value) {
  if (value == 0 || value == 360) return ''
  else if (value == 90) return ''
  else if (value == 180) return ''
  else if (value == 270) return ''
  else if (value > 0 && value < 90) return ''
  else if (value > 90 && value < 180) return ''
  else if (value > 180 && value < 270) return ''
  else if (value > 270 && value < 360) return ''
  return ''
}
/**
 * element.table
 *
 * @export
 * @param {*} row
 * @param {*} col
 * @param {*} value 
 * @returns
 */
export function formatterTDirectionType(row, col, value) {
  return formatterDirectionType(value)
}
